.element__img {
  display: block;
  width: 100%;
  height: 100%;
  max-height: 282px;
  object-fit: cover;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  border-radius: 10px 10px 0 0;
}

.element__img:hover {
  cursor: pointer;
}
